import React from 'react';
import {
  Link
} from 'react-router';
import FooterStore from '../stores/FooterStore'
import FooterActions from '../actions/FooterActions';

class Footer extends React.Component {
  constructor(props) {
    super(props);
    this.state = FooterStore.getState();
    this.onChange = this.onChange.bind(this);
  }

  componentDidMount() {
    FooterStore.listen(this.onChange);

    //
    $(function() {
      if ($(window).scrollTop() > 400) {
        $('.top').css({
          display: 'block'
        });
      } else {
        $('.top').css({
          display: 'none'
        });
      }

      $(window).scroll(function() {
        if ($(window).scrollTop() > 400) {
          $(".top").fadeIn(800);
        } else {
          $(".top").fadeOut(800);
        }
      });

      $(".top").click(function() {
        $('body,html').animate({
          scrollTop: 0
        }, 400);
        return false;
      });
    });


  }

  componentWillUnmount() {
    FooterStore.unlisten(this.onChange);
  }

  onChange(state) {
    this.setState(state);
  }

  render() {
    return (
      <footer>
        <div className='container'>
          
          <div className='row clearfix'>
            <div className="row clearfix">
              <div className="col-xs-6" style={{lineHeight:'110px', paddingLeft: '50px'}}>
                <img src="/img/footer_logo.png" alt=""/>
              </div>
              <div className='col-xs-6'>
                <h2 className='lead'>
                  <strong style={{letterSpacing: '5px', color:'#fefefe'}}></strong>
                </h2>
              </div>
            </div>

            <div className="divider"></div>

            <div className="row clearfix">
              <div className="col-xs-6" style={{paddingTop: '30px'}}>
                <p>:&nbsp;&nbsp;&nbsp;3&nbsp;&nbsp;&nbsp;304</p>
                <p>:&nbsp;&nbsp;&nbsp;51684609</p>
              </div>
              <div className='col-xs-6' style={{paddingTop: '30px', paddingBottom: '30px', textAlign:'center'}}>
                <p >&copy;2017&nbsp;&nbsp;&nbsp;</p>
                <p>&nbsp;&nbsp;110402430059 &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;ICP12010520-2</p>
              </div>
            </div>
          </div>
        </div>
        <div className="top">
          
        </div>
      </footer>
    );
  }
}

export default Footer;