import React from 'react';
import ProjectActions from '../actions/ProjectActions';
import ProjectStore from '../stores/ProjectStore';

class Project extends React.Component {
    constructor(props) {
        super(props);
        this.state = ProjectStore.getState();
        this.onChange = this.onChange.bind(this);
    }

    componentDidMount() {
        $(document).ready(function() {
            $("span.div1").hide();
            $(".sis-li li").hover(function() {
                $("span.div1", this).stop().slideToggle(500);
            });
        });

        ProjectStore.listen(this.onChange);

    }

    componentWillUnmount() {
        ProjectStore.unlisten(this.onChange);
    }

    onChange(state) {
        this.setState(state);
    }

    render() {

        let projects = [{
            title: '',
            projects: [{
                img: '/img/xiaodou1.png',
                info: '',
                title: '',
                url: 'http://192.168.10.200'
            }, {
                img: '/img/zhongchuang.jpg',
                info: '',
                title: '',
                url: 'http://192.168.10.100:8080'
            }, {
                img: '/img/pingtai1.png',
                info: '',
                title: '',
                url: '/'
            }, {
                img: '/img/pingtai.png',
                info: '',
                title: '',
                url: ''
            }, {
                img: '/img/pingtai.png',
                info: '',
                title: '',
                url: ''
            }]
        }, {
            title: '',
            projects: [{
                img: '/img/1.jpg',
                info: '',
                title: ''
            }, {
                img: '/img/2.jpg',
                info: '',
                title: '',
                url: ''
            }, {
                img: '/img/2.jpg',
                info: `APP
                ,
                `,
                title: '5G',
                url: ''

            }, {
                img: '/img/2.jpg',
                info: '',
                title: '',
                url: ''
            }]
        }];

        let projectList = projects.map((project, index) => {
            var doing = project.title == '' ? false : true;

            let blockList = project.projects.map((pro, index) => {
                let imgstyle = {
                    height: '250px',
                    overflow: 'hidden',
                    background: `url(${pro.img})`,
                    backgroundSize: 'cover'
                };

                if (pro.url) {
                    var link = `<a href="${pro.url}" target="_blank">${pro.title}</a>`;
                } else {
                    var link = pro.title;
                }

                if (!doing) {

                    return (
                        <li className="col-xs-4" key={index} style={{marginBottom: '40px'}}>
                            <div style={{border:'1px solid #ddd', paddingBottom: '20px'}}>
                                <div className="v1">
                                    <a href="#">
                                        <div style={imgstyle}>
                                        </div>
                                    </a>
                                    <span className="div1">
                                     <h3 style={{color: '#fff'}}> </h3> 
                                        <span className="txt">
                                            {pro.info}
                                        </span>
                                    </span>
                                </div>
                                <div style={{padding: '0 40px', marginTop:'30px'}}>
                                    <div style={{borderBottom: '1px solid #ddd'}}></div>
                                </div>
                                <div className="project-title" dangerouslySetInnerHTML={{__html:link}}></div>
                            </div>
                        </li>
                    );
                } else {
                    return (
                        <div className="col-xs-4">
                            <div className="doing">{pro.title}</div>
                        </div>
                    )
                }
            });

            return (
                <div className="outline clearfix" key={index}>
                    <h1 className="titon">{project.title}</h1>
                    <ul className="sis-li">
                      {blockList}  
                    </ul>
                </div>
            )
        });
        return (
            <div className="project">
                <div className="pic">
                </div>
                <div className="container">
                    <div className="row clearfix">
                        {projectList}
                    </div>
                </div>
            </div>

        );
    }
}

export default Project;